import React, { Component } from "react";
import Counters from "./counters.jsx";
import NavBar from "./navbar.jsx";

class Main extends Component {
  state = {
    counters: [
      { id: 1, value: 4 },
      { id: 2, value: 0 },
      { id: 3, value: 0 },
      { id: 4, value: 0 },
    ],
  };

  // Constructor to initialize properties of class
  constructor() {
    super();
    console.log("App - Constructor");
  }

  // Called after component is rendered into the DOM
  // Where you make ajax calls to get data from server
  componentDidMount() {
    // Ajax Call
    // this.setState({ });

    console.log("App - Mounted");
  }

  handleIncrement = (counter) => {
    const counters = [...this.state.counters]; // clones but uses the same
    const index = counters.indexOf(counter);
    counters[index] = { ...counter };
    counters[index].value++;
    this.setState({ counters });
  };

  handleReset = () => {
    const counters = this.state.counters.map((c) => {
      c.value = 0;
      return c;
    });
    this.setState({ counters });
  };

  handleDelete = (counterId) => {
    const counters = this.state.counters.filter((c) => c.id !== counterId);
    this.setState({ counters: counters });
  };
  render() {
    console.log("App - Rendered");
    return (
      <React.Fragment>
        <NavBar
          totalCounters={this.state.counters.filter((c) => c.value > 0).length}
        />
        <main className="container">
          <Counters
            counters={this.state.counters}
            onReset={this.handleReset}
            onIncrement={this.handleIncrement}
            onDelete={this.handleDelete}
          />
        </main>
      </React.Fragment>
    );
  }
}

export default Main;
